<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">打包复核</h1>
            
          <label for="">订单号</label><el-input v-model="task.order_no" placeholder="请输入订单号"></el-input>
          <label for="">SKU</label><el-input v-model="task.skus" placeholder="备注"></el-input> 
           
        </el-col>
      </el-row>
     
      <h2 class="title2">订单号：<span> {{ task.order_no }}</span></h2>
      <div class="tableContainer">
      <el-table
      :data="tableData"
       >
      <el-table-column
        prop="sku"
        label="SKU"
        width="180">
      </el-table-column>
      <el-table-column
        prop="num"
        label="数量"
        width="180">
      </el-table-column> 
      </el-table>
      </div>

       <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交复核</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "addTask",
      data() {
          return { 
            pageName: '添加',
            task: {
              order_no: "1",
              skus: "1", 
            },
            
             tableData: [{
            "sku": "AAA1",
            "num": "12", 
            },
            {
              "sku": "AAA2",
              "num": "11", 
            }
            ],
          }
      },
      methods: {
        submitTask() {
          let url = "http://api.orkowms.me/so-bill/check";
          let params = {};
          params.order_no = this.task.order_no;
          params.skus = JSON.stringify(this.tableData); 
           
          let that = this;  
          this.$axios.post(url,params).then((res) => { 
             console.log(res.data.message);
             if(res.data.code==0){
              that.$message({
                type: 'error',
                message: res.data.message
              }); 
            }else{
              that.$message({
                type: 'success',
                message: res.data.message
              }); 
            }
              
            
          }).catch((err) => { 
             console.log(err);
              that.$message({
                type: 'error',
                message: '上传失败!'
              }); 
          })
        },
         
      },
      created() {
         this.task.order_no = '';
          this.task.skus = ''; 
      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    .tableContainer
      margin 30px 30px 0px 30px
    .title2
      margin 10px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



